
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {getCookie,dateFormat} from '@/tool/common.js';
import Drive from '@/tool/classFactory/drive.js';
export default {
components: {
Group,
Cell
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          
that.changeFixed(this.clientHeight)
window.onresize = function temp() {
this.clientHeight = `${document.documentElement.clientHeight}`;
};
},
watch: {

clientHeight: function () {
this.changeFixed(this.clientHeight)
}
},
data () {
return {
params:{
'sessionId':''
},
params02:{
'type':'',
'sessionId':''
},
examMsg:{
'endTimeLong':'',
'startTimeLong':'',
'sorce':''
},
examInfo:{
},
hour:'',
minute:'',
second:''
}
},
created () {
this.params02.type = this.$route.query.type || null
this.params02.sessionId = this.params.sessionId = getCookie('sessionId')
this.examMsg.endTimeLong = this.$route.query.endTimeLong || null
this.examMsg.startTimeLong = this.$route.query.startTimeLong || null
this.examMsg.sorce = this.$route.query.sorce || null

console.log(this.$route.query.sorce)
console.log(this.examMsg)
this.getExamInfo()
this.timeDiffer(this.examMsg.startTimeLong,this.examMsg.endTimeLong)
},
methods: {
changeFixed(clientHeight) {                        //动态修改样式
console.log(clientHeight);
this.$refs.homePage.style.height = clientHeight + 'px';

},
getExamInfo (e){
const that = this;
Drive.prototype.getExamInfo(that.params02).then(res => {
let list = res.data.data || {};
that.examInfo = list

});
},
handIn (e){
const that = this;
Drive.prototype.handIn(that.params).then(res => {
let list = res.data.data || {};
that.examMsg = list

console.log(dateFormat(list.endTimeLong,true))
console.log(dateFormat(list.startTimeLong,true))
that.timeDiffer(list.startTimeLong,list.endTimeLong)

});
},
timeDiffer(begin,end){
var runTime = parseInt((end - begin) / 1000)
var year = Math.floor(runTime / 86400 / 365)
runTime = runTime % (86400 * 365)
var month = Math.floor(runTime / 86400 / 30)
runTime = runTime % (86400 * 30)
var day = Math.floor(runTime / 86400)
runTime = runTime % 86400
var hour = Math.floor(runTime / 3600)
runTime = runTime % 3600
var minute = Math.floor(runTime / 60)
runTime = runTime % 60
var second = runTime;

this.hour = hour
this.minute = minute
this.second = second

},
onErrorTap(){
this.$router.push('../error')
},
onRetestTap(){
const that = this
var redo = 1
this.$router.push('../question?redo='+ redo + '&&mid=' + that.examMsg.mid)
}
}
}



